.App{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background:coral;
  height: 100vh;
  width: 100vw;
}

.wrapper{
  padding:25px;
  border:0.1px solid grey;
  display: flex;
  flex-direction: column;
  width: 40%;
  height:auto;
  background: rgba(219,216,216,0.25);
  box-shadow: 0 8px 32px 0 rgba(31,28,135,0.37);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255,255,255,0.18);
}

.app_image{
  margin: 24px;
  max-height: 500px;
  width: auto;
  border-radius: 10px;
}